var angularApp = angular.module('angularApp');

var MainController = function($scope, $location, anchorSmoothScroll, GitHubBadgeService) {

    $scope.gotoElement = function (eID){
        // set the location.hash to the id of
        // the element you wish to scroll to.
        //$location.hash(eID);

        // call $anchorScroll()
        anchorSmoothScroll.scrollTo(eID);

    };

    $scope.showProfile1 = false;
    $scope.toggleShowProfile1 = function(){
        $scope.showProfile1 = !$scope.showProfile1;
    };

    $scope.showProfile2 = false;
    $scope.toggleShowProfile2 = function(){
        $scope.showProfile2 = !$scope.showProfile2;
    };

    $scope.showProfile3 = false;
    $scope.toggleShowProfile3 = function(){
        $scope.showProfile3 = !$scope.showProfile3;
    };

    $scope.showProfile4 = false;
    $scope.toggleShowProfile4 = function(){
        $scope.showProfile4 = !$scope.showProfile4;
    };

    $scope.showProfile5 = false;
    $scope.toggleShowProfile5 = function(){
        $scope.showProfile5 = !$scope.showProfile5;
    };

    $scope.showProfile6 = false;
    $scope.toggleShowProfile6 = function(){
        $scope.showProfile6 = !$scope.showProfile6;
    };

    $scope.showTool1 = false;
    $scope.toggleShowTool1 = function(){
        $scope.showTool1 = !$scope.showTool1;
    };

    $scope.showTool2 = false;
    $scope.toggleShowTool2 = function(){
        $scope.showTool2 = !$scope.showTool2;
    };

    $scope.showTool3 = false;
    $scope.toggleShowTool3 = function(){
        $scope.showTool3 = !$scope.showTool3;
    };

    $scope.showTool4 = false;
    $scope.toggleShowTool4 = function(){
        $scope.showTool4 = !$scope.showTool4;
    };

    $scope.showTool5 = false;
    $scope.toggleShowTool5 = function(){
        $scope.showTool5 = !$scope.showTool5;
    };

    $scope.showTool6 = false;
    $scope.toggleShowTool6 = function(){
        $scope.showTool6 = !$scope.showTool6;
    };
};
MainController.$inject = ['$scope', '$location', 'anchorSmoothScroll', 'GitHubBadgeService'];
angularApp.controller('MainController', MainController);